import React from "react";

const PricingContent = [
  {
    packName: "Bronze",
    packDetails: "For the Individual Creator",
    bgColor: "#FFF7EB",
    price: "5",
    durationNumber: "Bi-Daily Checks",
    facility: "Instant Email Alerts",
    features: [
      "Monitor 1 Channel",
      "Scan up to 100 videos per channel",
      "Link checks every 48 hours",
      "Email Alerts for Broken Links",
    ],
    animationDelay: "",
    activeClass: "",
    paymentLink: "https://buy.stripe.com/28oeXq2vKg705AQ8wy"
    // paymentLink: "https://buy.stripe.com/test_5kA8xI4cHgfJdKU6or",
  },
  {
    packName: "Silver",
    packDetails: "For the Growing Creator",
    bgColor: "#E2F2FD",
    price: "15",
    durationNumber: "Daily Checks",
    facility: "Multi-Channel Support",
    features: [
      "Monitor up to 5 Channels",
      "Scan up to 250 videos per channel",
      "Link checks every 24 hours",
      "Email Alerts for Broken Links"
    ],
    animationDelay: "100",
    activeClass: "active most-popular",
    paymentLink: "https://buy.stripe.com/3cs5mQ8U8dYS5AQeUV",
    // paymentLink: "https://buy.stripe.com/test_00g8xI5gL6F95eoaEI",
  },
  {
    packName: "Gold",
    packDetails: "For the Professional Creator",
    bgColor: "#FFEBEB",
    price: "30",
    durationNumber: "Half-Day Checks",
    facility: "Priority Support",
    features: [
      "Monitor up to 15 Channels",
      "Scan up to 500 videos per channel",
      "Link checks every 12 hours",
      "Email Alerts for Broken Links",
    ],
    animationDelay: "200",
    activeClass: "",
    paymentLink: "https://buy.stripe.com/eVa6qU2vK8EyaVacMM",
    // paymentLink: "https://buy.stripe.com/test_bIYdS210ve7B6isdQV",
  },
];

const PricingMonthly = () => {
  return (
    <div className="row justify-content-center">
      {PricingContent.map((val, i) => (
        <div
          className="col-lg-4 col-sm-6"
          data-aos="fade-up"
          data-aos-duration="1200"
          data-aos-delay={val.animationDelay}
          key={i}
        >
          <div className={`pr-table-wrapper ${val.activeClass}`}>
            <div className="pack-name">{val.packName}</div>
            <div className="pack-details">{val.packDetails}</div>
            <div
              className="top-banner d-md-flex"
              style={{ background: val.bgColor }}
            >
              <div className="price">
                <sup>$</sup>
                {val.price}
              </div>
              <div>
                <span>{val.durationNumber}</span>
                <em>{val.facility}</em>
              </div>
            </div>
            {/* /.top-banner */}
            <ul className="pr-feature">
              {val.features.map((list, i) => (
                <li key={i}>{list}</li>
              ))}
            </ul>
            <a href={val.paymentLink} target="_blank" className="trial-button">
              Join now!
            </a>
            {/* <div className="trial-text">No card required, cancel any time</div> */}
          </div>
          {/* /.pr-table-wrapper */}
        </div>
      ))}
    </div>
  );
};

export default PricingMonthly;
